* {
  box-sizing: border-box;
}

body {
  color: #fafafa;
  background-color: #212121;
  font-family: Roboto, sans-serif;
  font-size: 18px;
  line-height: 1.5;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

h1, p {
  margin: 0;
}

h1, p:not(:last-child) {
  padding-bottom: 20px;
}

.breed-select {
  width: 300px;
  max-width: 580px;
  height: 40px;
  justify-content: center;
  margin: 20px auto;
  padding: 0 20px;
