.error {
  display: none;
}

.cat-info {
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

.cat-container {
  justify-content: center;
  gap: 20px;
  display: flex;
}

.text {
  max-width: 600px;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 3px solid #fff;
  border-style: solid solid dotted dotted;
  border-radius: 50%;
  animation: rotation 2s linear infinite;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 24px;
  height: 24px;
  transform-origin: center;
  border: 3px solid #ff3d00;
  border-bottom-style: dotted;
  border-radius: 50%;
  margin: auto;
  animation: rotationBack 1s linear infinite;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes rotationBack {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(-360deg);
  }
}

